import { gql } from '@apollo/client';

// Add queries

const GET_USERS = gql`
query getUsers {
    users {
        id,
        email
    }
}
`;

const GET_CATEGORIES = `
query getCategories($registryId: Int!){
  categories(registryId:$registryId){
    id,
    categoryName,
    isRoot
    subCategories{
      id,
      categoryName,
      addedItemsCount,
      totalItemsCount
    }
    addedItemsCount,
    totalItemsCount
  }
 }
`;

const GET_PRODUCTS = `
query registryItems($registryId:Int!){
    registryItems(registryId:$registryId){
      edges{
        node{
          id,
          isGroupGift,
          isReserved,
          quantity,
          mostWanted,
          orderNo,
          giftMessage,
          isReserved,
          isAmountReceived,
          isPurchased
          item{
            id,
            title,
            shopifyItemId,
            note,

            category {
              id,
              categoryName,
              isRoot,

            }
          }
        }
      }
    }
  }

`


export {GET_USERS, GET_CATEGORIES, GET_PRODUCTS};

