import { MY_REGISTRY_ACTION_TYPES } from "./MyRegistryActions";
import { combineReducers } from "redux";
import defaultData from "data/ReducerDefaultData.json";

// Functions
const incrementProductQuantity = (state, payload) => {
  let {
    id,
    quantity
  } = payload;

  let stateInc = [...state];
  let newState = [];
  for (let product of stateInc) {
    if (product.id === id) {
      product.quantity += quantity;
    }
    newState.push(product);
  }
  return newState;
}

const decrementProductQuantity = (state, payload) => {
  let {
    id,
    quantity
  } = payload;
  let stateDec = [...state];
  let newState = [];
  for (let product of stateDec) {
    if (product.id === id && product.quantity - quantity > 0) {
      product.quantity -= quantity;
    }
    newState.push(product);
  }
  return newState;
}

const likeAddedProduct = (state, payload) => {
  let newState = [];
  for (let product of state) {
    if (product.id === payload) {
      product.favorite = !product.favorite
    }
    newState.push(product);
  }
  return newState;
}

const addProductToCart = (state, payload) => {
  for (let product of state) {
    if (product.id === payload.id) {
      return state
    }
  }
  state.push(payload)
  return state;
}


const setParentFilter = (state, payload) => {
  let newState = {...state}
  newState.parentFilter = payload
  newState.childFilter = [...state.childFilter]

  return newState
}


const setChildFilter = (state, payload) => {
  let newState = {...state}
  newState.childFilter = [...payload]

  return newState
}


// Reducers

// const products = (state = defaultData.myRegistry.defaultProduct, action) => {
const recommendedPrducts = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.ADD_RECOMMENDED_PRODUCT:
      return [...state, action.payload];
    case MY_REGISTRY_ACTION_TYPES.INCREMENT_QUANTITY:
      return incrementProductQuantity(state, action.payload);
    case MY_REGISTRY_ACTION_TYPES.DECREMENT_QUANTITY:
      return decrementProductQuantity(state, action.payload);
    default:
      return state;
  }
}

// const addedProducts = (state = defaultData.myRegistry.defaultAddedProduct, action) => {
  const addedProducts = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_REGISTRY_PRODUCTS:
      return action.payload;
    case MY_REGISTRY_ACTION_TYPES.LIKE_ADDED_PRODUCT:
      return likeAddedProduct([...state], action.payload);
    case MY_REGISTRY_ACTION_TYPES.ADD_PRODUCT_TO_CART:
      return addProductToCart([...state], action.payload);
    case MY_REGISTRY_ACTION_TYPES.REMOVE_PRODUCT_FROM_CART:
      return state.filter((item, _ ) => {
        return item.id !== action.payload
      })
    default:
      return state;
  }
}

const activeRecommandation = (state = "Bassinet", action) => {
  switch(action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_ACTIVE_RECOMMANDATION:
      return action.payload;
    default:
      return state;
  }
}

const filteredProducts = (state= [], action) => {
  switch(action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_FILTERED_PRODUCTS:
      return [...action.payload]
    default:
      return state
  }
}

const filterCategory = (state=[], action) => {
  switch(action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_FILTER_CATEGORY:
      return action.payload
    default:
      return state
  }
}

const filterApplied = (state=defaultData.myRegistry.defaultFilter, action) => {
  switch(action.type){
    case MY_REGISTRY_ACTION_TYPES.SET_PARENT_FILTER:
      return setParentFilter(state, action.payload)
    case MY_REGISTRY_ACTION_TYPES.SET_CHILD_FILTER:
      return setChildFilter(state, action.payload)
    case MY_REGISTRY_ACTION_TYPES.CLEAR_FILTER:
      return defaultData.myRegistry.defaultFilter
    default:
      return state;
  }
}
const checkList = (state = [], action) => {
  switch(action.type) {
    case MY_REGISTRY_ACTION_TYPES.GET_CHECKLIST_ITEMS:
      return action.payload;
    default:
      return state;
  }
}


export default combineReducers({
  recommendedPrducts,
  addedProducts,
  activeRecommandation,
  filterCategory,
  filteredProducts,
  filterApplied,
  checkList
})